import { Component, OnInit, Input } from '@angular/core';
import template from './fox-partido.component.html'
import style from './fox-partido.component.css'
import axios from 'axios'

@Component({
  selector: 'app-fox-partido',
  template: template + '',
  styles: [style + '']
})
export class FoxPartidoComponent implements OnInit {
  @Input() showtitle: boolean = true;
  @Input() competitionid: string = '';
  @Input() matchid: string = '';
  @Input() lang: string = 'es';
  @Input() max: number = 0;
  @Input() country: string = 'ar';

  @Input() onsummary: Function;
  @Input() onmintomin: Function;
  @Input() onstats: Function;
  @Input() onalignments: Function;
  @Input() onplayer: Function;
  @Input() oninit: Function;
  @Input() onerror: Function;

  title = 'Partido';
  activeTab = 's'
  activeAlign = true;
  hasEnded = false
  hasNotStarted = false
  nothingHappen = false

  // HEADER PROPS
  data = {
    'home-team': {},
    'visiting-team': {},
    'game-state': {}
  }

  // SUMMARY PROPS
  stats = {
    plays: []
  }
  plays = []
  homeTeamId = ''
  visitingTeamId = ''
  showAll = true

  // STATS PROPS
  estadisticas = []
  objectKeys = Object.keys
  parseInt = Number.parseInt

  // ALIGNMENTS PROPS
  hometeam = {
    'team-info': {},
    'manager-info': {}
  }
  homeplayers = {
    goalkeeper: [
      {}
    ],
    midfielder: [],
    defender: [],
    forward: []
  }
  visitteam = {
    'team-info': {},
    'manager-info': {}
  }
  visitplayers = {
    goalkeeper: [
      {}
    ],
    midfielder: [],
    defender: [],
    forward: []
  }

  constructor() {}

  ngOnInit() {
    // Usefull variables
    const _this = this;
    const params = {};

    // API Params setup
    if (_this.country) params['country-code'] = _this.country;
    if (_this.matchid) params['match-id'] = _this.matchid;


    axios.get('https://stats.foxsportsla.com/stats/get_play_to_play_live', {
      params: params
    }).then(function(resp) {

      // HEADER DATA
      _this.data = resp.data

      // SUMMARY DATA
      _this.stats = resp.data
      _this.plays = resp.data.plays.splice(0, 10)
      _this.visitingTeamId = resp.data['visiting-team']._id
      _this.homeTeamId = resp.data['home-team']._id

      _this.nothingHappen = _this.plays.length === 0;
      _this.hasEnded = resp.data['game-state']['status'] === 'final';
      _this.hasNotStarted = resp.data['game-state']['status'] !== 'final' && resp.data['game-state']['live-minute'] === 0
      if (!_this.hasEnded && resp.data['game-state']['live-minute'] <= 15) _this.all()

      if (_this.hasNotStarted) _this.activeTab = 'a'
      else if (_this.hasEnded) _this.activeTab = 's'
      else if (_this.nothingHappen) _this.activeTab = 'm'

      axios.get('https://stats.foxsportsla.com/stats/get_statistics_live', {
        params: params
      }).then(function(resp) {

        // STATS DATA
        _this.estadisticas = resp.data['home-team'].map( item => {
          return {
            home: item['stats-value'],
            label: _this.label(item['stats-label']),
            visit: resp.data['visiting-team'].find(v => v['stats-label'] == item['stats-label'])['stats-value']
          }
        });


        axios.get('https://stats.foxsportsla.com/stats/get_teams_detail', {
          params: {
            'competition-id': _this.competitionid,
            'team-id': _this.data['home-team']['_id'],
            'country-code': _this.country
          }
        }).then(function(resp) {

          _this.hometeam = resp.data

          resp.data.players.forEach(player => {
            if (player.position === 'goalkeeper') _this.homeplayers[player.position][0] = player
            else _this.homeplayers[player.position].push(player)
          })


          axios.get('https://stats.foxsportsla.com/stats/get_teams_detail', {
            params: {
              'competition-id': _this.competitionid,
              'team-id': _this.data['visiting-team']['_id'],
              'country-code': _this.country
            }
          }).then(function(resp) {

            _this.visitteam = resp.data

            resp.data.players.forEach(player => {
              if (player.position === 'goalkeeper') _this.visitplayers[player.position][0] = player
              else _this.visitplayers[player.position].push(player)
            })


            if (_this.oninit) _this.oninit(_this.data);

          }).catch(err => {
            if (_this.onerror) _this.onerror(err);
          })

        }).catch(err => {
          if (_this.onerror) _this.onerror(err);
        })

      }).catch(err => {
        if (_this.onerror) _this.onerror(err);
      })
    }).catch(err => {
      if (_this.onerror) _this.onerror(err);
    })
  }

  changeAlign (home) {
    this.activeAlign = home;
  }

  toggleTab(tab) {
    this.activeTab = tab;
    if (tab === "s") {
      this.onsummary(this.data);
    } else if (tab === 'a') {
      this.onalignments(this.data);
    } else if (tab === 'e') {
      this.onstats(this.data);
    } else if (tab === 'm') {
      this.onmintomin(this.data);
    }
  }

  all() {
    this.plays = this.stats.plays
    this.showAll = false
  }

  icon(text) {
    return {
      'start-half': 'icon-ball',
      'foul': 'icon-ball',
      'free-kick': 'icon-shoe',
      'shot-on-goal': 'icon-ball',
      'corner-kick': 'icon-shoe',
      'cross': 'icon-ball',
      'offside': 'icon-flag',
      'throw-in': 'icon-ball',
      'goal': 'icon-ball',
      'yellow-card': 'icon-yellow_card',
      'shot': 'icon-ball',
      'goal-kick': 'icon-shoe',
      'red-card': 'icon-ball',
      'half-over': 'icon-ball',
      'substitution': 'icon-flag',
      'game-over': 'icon-ball'
    }[text];
  }

  label (text) {
    return {
      'goal': 'Goles',
      'yellow-card': 'Tarjetas Amarillas',
      'play-clear': 'Jugadas Claras',
      'corner-kick': 'Tiros de esquina',
      'cross': 'Pases',
      'red-card': 'Tarjetas Rojas',
      'foul': 'Faltas',
      'free-kick': 'Tiros Libres',
      'game-over': 'Final de juego',
      'half-over': 'Medio tiempo',
      'injury': 'Lesiones',
      'offside': 'Offsides',
      'penalti-ok': 'Penales convertidos',
      'penalti-not-ok': 'Penales errados',
      'shot': 'Tiros',
      'shot-on-goal': 'Tiros a gol',
      'start-half': 'Primera mitad',
      'substitution': 'Sustituciones',
      'own-goal': 'Goles en contra',
      'goal-kick': 'Saques de arco',
      'throw-in': 'Laterales',
      'shotout-goal': 'Tiros al arco',
      'shotout-save': 'Atajadas',
      'shotout miss': 'Tiros afuera'
    }[text];
  }

}
